@import url(https://fonts.googleapis.com/css2?family=Prompt:wght@300&display=swap);
* {
  margin: 0;
  scroll-behavior: smooth;
  padding: 0;
  box-sizing: border-box;
  scrollbar-base-color: #272c33;
  
}
:root{
  --black: #000;
  --dark: #131516;
  --brightDark: #272c33;
  --lightWhite: #d9dff1;
  --darkWhite: #c6c6ca;
  --darkGrey: #3b3b3b;
  --lightGrey: #454a4d;
  --brightGrey: rgb(30, 32, 33);
  --scrollbar: #CFD8DC;
  --scrollBarThumb: #90A4AE;
}

body{
  min-height: 100vh;
  background:#131516;
  background:var(--dark);
  font-family: 'Prompt', sans-serif;
  color: #d9dff1;
  color: var(--lightWhite);
  scrollbar-width: thin;
  position: relative;
  scrollbar-color: #90A4AE #CFD8DC;
  scrollbar-color: var(--scrollBarThumb) var(--scrollbar);
}

body::-webkit-scrollbar {
  width: 11px;
}

body::-webkit-scrollbar-track {
  background: #CFD8DC;
  background: var(--scrollbar);
}
body::-webkit-scrollbar-thumb {
  background-color: #90A4AE;
  background-color: var(--scrollBarThumb) ;
  border-radius: 6px;
  border: 3px solid #CFD8DC;
  border: 3px solid var(--scrollbar);
}

/* #region Main */
main{
  width: 100%;
  min-height: 100vh;
  background: #131516;
  background: var(--dark);
}

main > a{
  display: none;
}

main .arrowShow{
  display: inline;
}

main > a figure{
  position: fixed;
  width: 50px;
  height: 50px;
  background-color: #c6c6ca;
  background-color: var(--darkWhite);
  border-radius: 50%;
  right: 2%;
  right: 2%;
  top: 90vh;
  z-index: 0;
}
main > a figure img{
  width: 100%;
  height: 100%;
}
/* #endregion */


/* #region Header */
header{
  height: 60px;
  width: 100%;
  background: rgba(255, 0, 0, 0.288);
  padding: 0.5%;
  display: flex;
  position: fixed;
  z-index: 5;
}
header nav{
  display: flex;
  height: 100%;
  width: 100%;
}
header nav figure{
  display: inline;
  justify-content: start;
  width: 15%;
  height: 100%;
  cursor: pointer;
}
header nav figure img{
  width: 100%;
  height: 100%;
}
header nav ul{
  width: 15%;
  height: 100%;
  margin-left: 3%;
  display: flex;
  justify-content: center;
  align-items: center;
  list-style: none;
}
header nav ul li a:hover{
  color: #c6c6ca;
  color: var(--darkWhite);
  
}
header nav ul li:hover a{
  text-decoration: underline;
  transition: 1s;
  transition-delay: 1s;
}
header nav ul li{
  display: flex;
  justify-content: center;
  align-items: center;
  list-style: none;
  letter-spacing: 2px;
  margin-left: 13%;
  height: -webkit-max-content;
  height: max-content;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
}
header nav ul li a{
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  height: -webkit-max-content;
  height: max-content;
  text-decoration: none;
  color: #d9dff1;
  color: var(--lightWhite);
}

header button:hover p{
  color: #c6c6ca;
  color: var(--darkWhite);
}
header button{
  width: 10%;
  height: 80%;
  background-color: transparent;
  cursor: pointer;
  margin: auto;
  margin-right: 4%;
  display: flex;
  align-items: center;
  justify-content: space-between;
  border: none;
}

header button figure{
  margin-left: 20%;
  display: inline-block;
  width: 25%;
  height: 80%;
}

header button figure img{
  width: 100%;
  height: 100%;
}

header button p{
  display: inline-block;
  font-weight: 100;
  font-size: 1.4rem;
  letter-spacing: 1px;
  margin-right: 0.5em;
  color: #d9dff1;
  color: var(--lightWhite);
}
/* #endregion */


/* #region Aside */

aside{
  display: none;
}

.asideOn{
  display: inline;
  top: 75vh;
  left: 78%;
  width: 20%;
  height: 150px;
  position: fixed;
  background-color: #272c33;
  background-color: var(--brightDark);
  padding: 0.5%;
  border-radius: 20px;
  opacity: 80%;
  z-index: 1;
}
.asideOn figure{
  position: absolute;
  width: 30px;
  height: 30px;
  z-index: 2;
  right: -10px;
  top: -23px;
  cursor: pointer;
  overflow: visible;
  background-color: #d9dff1;
  background-color: var(--lightWhite);
  border-radius: 50%;
}
.asideOn figure img{
  width: 100%;
  height: 100%;
}
.asideOn h3{
  width: 90%;
  display: inline-block;
  font-size: 18px;
  letter-spacing: 2px;

}
.asideOn input:focus-visible{
  outline: none;
}
.asideOn input::-webkit-input-placeholder{
  color: #d9dff1;
  color: var(--lightWhite);
}
.asideOn input:-ms-input-placeholder{
  color: #d9dff1;
  color: var(--lightWhite);
}
.asideOn input::placeholder{
  color: #d9dff1;
  color: var(--lightWhite);
}
.asideOn input{
  width: 100%;
  height: 20%;
  background-color: #3b3b3b;
  color: #d9dff1;
  color: var(--lightWhite);
  border-radius: 5px;
  border: none;
  margin: 5px auto;
  padding: 10px 12px;
}
.asideOn p{
  font-size: x-small;
  color: #d9dff1;
  color: var(--lightWhite);
}
.asideOn p u:hover{
  cursor: pointer;
  color: #c6c6ca;
  color: var(--darkWhite);
}

/* #endregion */


/* #region Signboard */
main section:first-child{
  padding: 10% 10% 10% 15%;
  width: 100%;
  height: 700px;
  position: relative;
}
main section:first-child video{
  position: absolute;
  top: 20%;
  left: 0;
  height: 70%;
  width: 100%;
  object-fit: cover;
}
main section:first-child div{
  position: absolute;
  width: -webkit-max-content;
  width: max-content;
  margin-top: 2%;
}

main section:first-child h1{
  display: inline-block;
  width: 15%;
  font-size: 200%;
  letter-spacing: 20px;
  text-transform: uppercase;
  font-weight: 900;
}
main section:first-child h2{
  width: 30%;
  font-size: 100%;
  letter-spacing: 4px;

}
main section:first-child button{
  width: 140px;
  height: 40px;
  cursor: pointer;
  border: none;
  border-radius: 10px;
  transition: 0.5s;
  background-color: #000;
  background-color: var(--black);
  margin: 2em 1em 0 0;

}
main section:first-child button:hover{
  box-shadow: 0px 0px 10px 0.5px #d9dff1;
  box-shadow: 0px 0px 10px 0.5px var(--lightWhite);
  transition: 0.5s;
}

main section:first-child button figure{
  border-radius: 5px;
  width: 100%;
  height: 100%;

}


main section:first-child button figure img{
  cursor: pointer;
  width: 100%;
  height: 100%;
  border-radius: 5px;

}
/* #endregion */


/* #region Information */
main section:nth-child(2) {
  width: 100%;
  margin: 0 auto;
  background-color: #272c33;
  background-color: var(--brightDark);
}
main section:nth-child(2) h2{
  width: 100%;
  text-align: center;
  display: inline-block;
  margin: 1em auto;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
}
main section:nth-child(2) p{
  display: inline-block;
  text-align: center;
  width: 80%;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  letter-spacing: 2px;
  word-spacing: 2px;
  margin: 0 10% 1.5em 10%;
}

/* #endregion */


/* #region gridTricks */
main section:nth-child(3) {
  width: 100%;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
}

main section:nth-child(3) h2{
  text-align: center;
  margin: 3% 0;
  color: #d9dff1;
  color: var(--lightWhite);
  text-transform: uppercase;
  letter-spacing: 2px;
}

main section:nth-child(3) ul{
  width: 90%;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  display: grid;
  grid-template-rows: 4,1fr;
  grid-template-columns: repeat(3,1fr);
  margin: 3% auto;
  grid-gap: 10px;
  gap: 10px;
  grid-row-gap: 30px;
  row-gap: 30px;
  text-align: center;

}
main section:nth-child(3) ul li{
  list-style: none;
  text-align: center;
}


main section:nth-child(3) ul li article h3{
  margin-bottom: 3%;
  text-align: center;

}

main section:nth-child(3) ul li article p{
  display: flex;
  margin: 3% auto;
  align-items: center;
  justify-content: center;
  width: 80%;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
}
main section:nth-child(3) ul li article button{
  padding: 2%;
  background-color: #3b3b3b;
  background-color: var(--darkGrey);
  border: none;
  border-radius: 5px;

}
main section:nth-child(3) ul li article button:hover{
  background-color: #272c33;
  background-color: var(--brightDark);
  box-shadow: 0px 0px 10px 0.5px #d9dff1;
  box-shadow: 0px 0px 10px 0.5px var(--lightWhite);

  transition: 0.5s;

}
main section:nth-child(3) ul li article button:hover a{
  text-decoration: underline;
  transition: 0.5s;
}
main section:nth-child(3) ul li article button a{
  text-decoration: none;
  color: #d9dff1;
  color: var(--lightWhite);
  letter-spacing: 1px;
  font-size: larger;
}
main section:nth-child(3) ul li article figure{
  width: 300px;
  height: 200px;
  margin: 0 auto;
}
main section:nth-child(3) ul li article figure img{
  border-radius: 10px;
  width: 100%;
  height: 100%;
}
/* #endregion */


/* #region Form */
#Form{
  width: 100%;
  background-color: #272c33;
  background-color: var(--brightDark);
  height: 400px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
#Form form{
  display: inline-block;
  width: 40%;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  position: relative;
}
#Form form input{
  display: block;
  margin: 1em auto;
  width: 60%;
  height: 50px;
  margin-bottom: 1em;
  background-color: #454a4d;
  background-color: var(--lightGrey);
  border-radius: 15px;
  border: none;
}
#Form form input:hover{
  box-shadow: 0px 0px 10px 0.5px #c6c6ca;
  box-shadow: 0px 0px 10px 0.5px var(--darkWhite);
  transition: 0.5s;
}
#Form form input:focus-visible{
  outline: none;
  box-shadow: 0px 0px 10px 1px #90A4AE;
  box-shadow: 0px 0px 10px 1px var(--scrollBarThumb);
  padding-left: 5%;
  transition: 0.5s;
}

#Form form label{
  color: #000;
  margin-left: 20%;
  letter-spacing: 1px;
  font-weight: 400;
} 

form button[type="submit"]{
  display: block;
  margin: 2em auto;
  width: 60%;
  height: 50px;
  margin-bottom: 1em;
  background-color: #454a4d;
  background-color: var(--lightGrey);
  border-radius: 15px;
  border: none;
  letter-spacing: 2px;
  font-weight: 500;
  color: #c6c6ca;
  color: var(--darkWhite);
}
form button[type="submit"]:hover{
  background-color: #3b3b3b;
  background-color: var(--darkGrey);
  color: #d9dff1;
  color: var(--lightWhite);
  transition: 0.5s;

}

#Form form label:last-child{
  position: static;
  position: initial;
  display: inline-block;
  width: 50%;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  font-size: small;
  margin: 0 auto;
}
#Form form label:last-child:hover{
  
  text-decoration: underline;
  transition: 0.5s;
}
#Form form label:last-child:hover u{
  color: #90A4AE;
  color: var(--scrollBarThumb);
  cursor: pointer;
}
#Form form input:nth-child(6){
  display: inline-block;
  width: 20px;
  height: 20px;
  background-color: transparent;
  margin: auto 5% auto 25%;
}
/* #endregion */


/* #region Footer*/
footer{
  background-color: #000;
  background-color: var(--black);
  height: 450px;
  display: flex;
  flex-flow: wrap row;
  align-items: center;
  justify-content: center;
}
footer ul p{
  display: block;
  text-align: left;
  width: 100%;
  margin: 3em 0;
}

footer ul {
  margin: 0 5%;
  height: 350px;
  width: 150px;
  display: flex;
  flex-flow: column wrap;
  align-items: left;
}

footer ul li {
  text-align: left;
  list-style: none;
  display: flex;
  flex-direction: column;
}
footer ul:last-child{
  margin: 0.5em 0;
}
footer ul li a:hover {
  text-decoration: underline;
  color: #c6c6ca;
  color: var(--darkWhite);
}

footer ul li a{
  letter-spacing: 1.5px;
  text-align: left;
  text-decoration: none;
  color: #3b3b3b;
  color: var(--darkGrey);
  margin-bottom: 0.5em;
}


footer ul:nth-child(3) li button{
  width: 140px;
  height: 50px;
  cursor: pointer;
  border-color: #000;
  border-color: var(--black);
  border-radius: 10px;
  transition: 0.5s;
  margin-bottom: 0.5em;
  background-color: #000;
  background-color: var(--black);
}
footer ul:nth-child(3) li button:last-child{
  border-radius: 15px;

}

footer ul:nth-child(3) li button figure{
  width: 100%;
  height: 100%;
  display: inline-block;
  cursor: pointer;
  transition: 0.5s;
  border-radius: 5px;
}
footer ul:nth-child(3) li button figure:last-child{
  border-radius: 15px;

}
footer ul:nth-child(3) li button figure:hover{
  box-shadow: 0px 0px 10px 0.5px #d9dff1;
  box-shadow: 0px 0px 10px 0.5px var(--lightWhite);
  transition: 0.5s;
}

footer ul:nth-child(3) li button figure img{
  cursor: pointer;
  width: 100%;
  height: 100%;
  border-radius: 5px;
}

footer > p{
  display: inline-block;
  text-align: center;
  width: 100%;
  
}

/* #endregion */

